import * as yup from 'yup'

export const addSchema = yup
  .object()
  .noUnknown()
  .shape({
    subject: yup.string().required("   'subject'"),
    description: yup.string().required("   'description'"),
    createdBy: yup.string().required("   'createdBy'"),
    startDate: yup.string().required("   'startDate'"),
    endDate: yup.string().required("   'endDate'"),
    cost: yup.number().required("   'cost'").positive(),
  })
  .strict()
